import { createRouter, createWebHistory } from 'vue-router';
import Home from '../section/Home.vue';
import About from '../section/About.vue';
import Stack from '../section/Stack.vue';
import Project from '../section/Project.vue';
import Education from '../section/Education.vue';
import Career from '../section/Career.vue';
import Contact from '../section/Contact.vue';
import Login from '../section/Login.vue';
import Projects from '../pages/Projects.vue';
import ProjectUpload from '../pages/ProjectUpload.vue';
import LoginPopup from '../components/LoginPopup.vue';

const routes = [
    { path: '/', component: Home },
    { path: '/about', component: About },
    { path: '/stack', component: Stack },
    { path: '/project', component: Project },
    { path: '/education', component: Education },
    { path: '/career', component: Career },
    { path: '/contact', component: Contact },
    { path: '/login', component: Login },
    { path: '/projects', component: Projects },
    {
        path: '/project-upload',
        component: ProjectUpload, // ÏóÖÎ°úÎìú ÌéòÏù¥ÏßÄ Í≤ΩÎ°ú
        meta: { requiresAdmin: true }, // Í¥ÄÎ¶¨Ïûê Í∂åÌïúÏù¥ ÌïÑÏöîÌïú Í≤ΩÎ°ú
    },
    { path: '/login-popup', component: LoginPopup },
];

const router = createRouter({
    history: createWebHistory('/Portfolio/'),
    routes,
});

// ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Í∞ÄÎìú Ï∂îÍ∞Ä
router.beforeEach((to, from, next) => {
    const isLoggedIn = localStorage.getItem('isLoggedIn') === 'true';
    const isAdmin = localStorage.getItem('isAdmin') === 'true';

    // Í¥ÄÎ¶¨Ïûê Í∂åÌïúÏù¥ ÌïÑÏöîÌïú ÌéòÏù¥ÏßÄ Ï≤òÎ¶¨
    if (to.meta.requiresAdmin) {
        if (!isLoggedIn) {
            alert('Please log in first.');
            return next('/login'); // Î°úÍ∑∏Ïù∏ Ïïà Îêú Í≤ΩÏö∞ Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú
        }
        if (!isAdmin) {
            alert('Access Denied: Admins only!');
            return next('/'); // Í¥ÄÎ¶¨ÏûêÍ∞Ä ÏïÑÎãå Í≤ΩÏö∞ ÌôàÏúºÎ°ú Î¶¨Îã§Ïù¥Î†âÌä∏
        }
    }

    next(); // Ï°∞Í±¥ Ï∂©Ï°± Ïãú Ï†ïÏÉÅÏ†ÅÏúºÎ°ú Ïù¥Îèô
});


export default router;
